/* src/components/InningBox.css */

#InningBox
.inning-box {
  display: flex;
  justify-content: center;
  font-size: small;
  padding-top: 0.25rem;
  font-size: 1.875rem; 
  /* border: 2px solid black; */
}

#InningBox
.inning-container {
    background-color: #006400;
    align-items: center;
    border-radius: 0.375rem; 
    color: white; 
    text-align: center;
    font-size: 1.875rem; 
    border: 2px inset rgb(255, 255, 255);
    
}
  
#InningBox
.inning-container, .inning-number, .arrow-box {
  font-size: 2rem;
}

#InningBox
.inning-header{
  font-size: small;
}


  

/**==============================================
 * *                InningBox 
 *   Inning Arrows stling
 *   
 *   
 *
 *=============================================**/
#InningBox
.inning-content {
    padding: 0.25rem;
    display: flex; 
    align-items:center;
    padding: 0.25rem;
    justify-content: center;
  }

  #InningBox
  .arrow-box {
    display:flex;
    width: 40px;
    height: 25px;
    justify-content: center;
    align-items: center;
  }
  
  #InningBox
  .top-arrow,
  .bottom-arrow {
    font-size-adjust: 24px;
  }
  
  #InningBox
  .illuminated {
    color: white; 
  }
  
  #InningBox
  .not-illuminated {
    color: rgb(87, 85, 85);
  }
  
  /**=======================================================================================================================
   *                                                     Responsive Design 
   *=======================================================================================================================**/
  /* SECTION - Tablets -- Adjust font size for tablets */
    /* TODO - Figure out the width bounds needed for tablet screens */

    @media (max-width: 768px) {
        .inning-container, .inning-number, .arrow-box {
            font-size: 2rem; 
            }
        }    

  /* SECTION - Mobile devices -- Adjust font size for mobile devicees */
  /* TODO - Figure out the width bounds needed for mobile screens */
    @media (max-width: 480px) {
        .inning-container, .inning-number, .arrow-box {
        font-size: 2rem; 
        }
    }